b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video,
button,
br {
  vertical-align: baseline;
  margin: 0;
  outline: transparent;
  border: 0;
  padding: 0;
  box-sizing: border-box;
  font-weight: 400;
  font-size: 100%;
  background: transparent;
}

img {
  display: inline-block;
  width: 100%;
  height: auto;
  object-fit: contain;
}

sub,
sup {
  margin: 0;
  outline: transparent;
  border: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 100%;
  background: transparent;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  outline: transparent;
  border: 0;
  padding: 0;
  font-size: 100%;
  background: transparent;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul,
li {
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
}

blockquote,
q {
  quotes: none;
}

:focus {
  outline: transparent;
}

ins {
  text-decoration: none;
}

del {
  text-decoration: line-through;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-family: "Space-Grotesk", sans-serif;
  letter-spacing: 0.03em;
}

a,
a:hover,
a:active {
  text-decoration: none;
}

a,
button,
input {
  cursor: pointer;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
  outline: transparent;
}

button {
  appearance: none;
  overflow: visible;
  margin: 0;
  border: none;
  padding: 0;
  width: auto;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  line-height: normal;
  background-color: transparent;
}

input,
input:hover,
input:focus,
input:active,
button,
button:hover,
button:focus,
button:active {
  outline: transparent;
  cursor: pointer;
}

input,
textarea {
  margin: 0;
  border: none;
  border-radius: 0;
  padding: 0;
  background-color: transparent;
}

textarea {
  font-family: "Satoshi", sans-serif;
  resize: none;
}

address {
  font-style: normal;
}

